@import url("https://fonts.googleapis.com/css?family=Roboto+Slab:100,300,400,700");
@import url("https://fonts.googleapis.com/css?family=Raleway:300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i");

* {
    margin: 0;
    border: 0;
    box-sizing: border-box;
}

:root {
    --font-roboto: "Roboto Slab", serif;
    --font-raleway: "Raleway", sans-serif;
}

body {
    font-family: var(--font-roboto);
    background-color: #190a5f;
}

.app {
    padding: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

a {
    text-decoration: none;
}

h1 {
    font-size: 3rem;
    letter-spacing: 0.9px;
    background: linear-gradient(
            90deg,
            rgb(216, 135, 105),
            rgba(221, 10, 10, 0) 100%
    );
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    width: fit-content;
}

h2 {
    padding-top: 10px;
    color: rgba(255, 255, 255, 0.712);
    font-weight: 300;
    font-family: var(--font-raleway);
}

.search {
    width: 71%;
    margin: 4rem 0 2rem;

    display: flex;
    align-items: center;
    justify-content: center;

    padding: 1.5rem 1.75rem;
    border-radius: 3rem;
    background: #190862;
    -webkit-box-shadow: 5px 5px 7px #02153b, -5px -5px 7px #041e30;
    box-shadow: 5px 5px 7px #031333, -5px -5px 7px #03243a;
}

.search input {
    flex: 1;
    border: none;
    font-size: 1.5rem;
    font-family: var(--font-raleway);
    font-weight: 500;
    padding-right: 1rem;

    outline: none;
    color: #a1a1a1;
    background: #190862;
}

.search img {
    width: 35px;
    height: 35px;
    cursor: pointer;
}

.options {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
}

.options .choices {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    border-radius: 30px;
    font-family: var(--font-raleway);
    background-color: rgba(255, 255, 255, 0.663);
    border: 3px solid rgb(210, 134, 10);
}

.options .choices:hover {
    background-color: rgba(255, 255, 255, 0.868);
    transition: all 0.5s ease-out;
}

.options .choices.active {
    background-color: rgb(234, 148, 9);
    border: 3px solid rgb(210, 134, 10);
    color: white;
    border-radius: 30px;
    font-weight: ;
}

.options .pop {
    border-radius: 30px;
    padding: 8px 15px;
    color: rgb(64, 64, 64);
    font-weight: 600;
    border: 3px solid rgb(234, 148, 9);
    background-color: rgb(230, 235, 243);
}

.options .pop.active {
    background-color: rgb(234, 148, 9);
    border: 3px solid rgb(234, 148, 9);
    color: white;
}

.options .top {
    padding: 8px 15px 8px 8px;
    font-weight: 500;
}

.empty {
    width: 100%;
    margin-top: 3rem;

    display: flex;
    justify-content: center;
    align-items: center;
}

.empty span {
    font-size: 1.25rem;
    color: #f9d3b4b2;
    font-family: var(--font-raleway);
}

.container {
    width: 100%;
    margin-top: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.movie {
    width: 310px;
    height: 460px;
    margin: 1.5rem;

    position: relative;
    border-radius: 12px;
    overflow: hidden;
    border: none;

    transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);
    box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
}

.movie div:nth-of-type(1) {
    position: absolute;
    padding: 16px;
    width: 100%;
    opacity: 0;
    top: 0;
    color: #f9d3b4;
}

.movie:hover {
    box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
    transform: scale(1.05, 1.05);
}

.movie div:nth-of-type(2) {
    width: 100%;
    height: 100%;
}

.movie div:nth-of-type(2) img {
    height: 100%;
    width: 100%;
}

.movie div:nth-of-type(3) {
    z-index: 2;
    background-color: #0310499f;
    padding: 45px 24px 24px 24px;
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
}

.movie div:nth-of-type(3) span {
    font-family: "Raleway", sans-serif;
    text-transform: uppercase;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: 500;
    color: #f0f0f0;
    bottom: 25px;
    position: absolute;
    -webkit-transition: all 400ms ease-out;
    -moz-transition: all 400ms ease-out;
    -o-transition: all 400ms ease-out;
    -ms-transition: all 400ms ease-out;
    transition: all 400ms ease-out;
}

.movie div:nth-of-type(3) p {
    position: absolute;
    margin-top: 5px;
    font-family: "Roboto Slab", serif;
    color: #f9d3b4;
    bottom: -100px;
    left: 0;
    text-align: center;
    opacity: 0;
    -webkit-transition: all 400ms ease-out;
    -moz-transition: all 400ms ease-out;
    -o-transition: all 400ms ease-out;
    -ms-transition: all 400ms ease-out;
    transition: all 400ms ease-out;
}

.movie:hover div:nth-of-type(3) span {
    opacity: 0;
}

.movie:hover div:nth-of-type(3) p {
    opacity: 1;
    bottom: 20px;
}

.movie:hover div:nth-of-type(2) {
    height: 100%;
    opacity: 0.3;
}

.movie:hover div:nth-of-type(3) {
    background-color: transparent;
}

.movie:hover div:nth-of-type(1) {
    opacity: 1;
}

.footer {
    position: relative;
    width: 100%;
    height: 10vh;
    display: flex;
    align-items: flex-end;
    justify-content: center;
}

.footer span {
    color: rgb(230, 235, 243);
    font-family: var(--font-raleway);
}

@media screen and (max-width: 600px) {
    .app {
        padding: 4rem 2rem;
    }

    .search {
        padding: 1rem 1.75rem;
        width: 100%;
    }

    .search input {
        font-size: 1rem;
    }

    .search img {
        width: 20px;
        height: 20px;
    }
}

@media screen and (max-width: 400px) {
    .app {
        padding: 4rem 1rem;
    }

    h1 {
        font-size: 2rem;
    }

    .container {
        margin-top: 2rem;
    }

    .movie {
        width: "100%";
        margin: 1rem;
    }
}
